.sidebar {
    background-color: white;

}

.sidebar.open {
    width: 15%;
    position: absolute;
    min-height: 100vh;
}

.background {
    background-color: #D9D9D9;
    opacity: .8;
    position: absolute;
    width: 100vw;
    height: 100vh;
}

.nav {
    display: flex;
    flex-direction: column;

    padding: 20px 0 20px 40px;
}

.nav a {
    padding: 20px;
    font-weight: 600;
    font-size: 22px;
    text-decoration: unset;
    color: black;
}

.nav a:hover {
    color: #58ACFF;
}

.nav a:active {
    color: #3D6B99;

}

.nav a.active {
    color: #0059B2;
    text-decoration: underline;

}


.close {
    background-color: unset;
    border: unset;
    width: 100%;
    text-align: end;
    padding-right: 20px;

}

.close img {
}
